@font-face {
  font-family: 'Barlow';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/barlow/v12/7cHqv4kjgoGqM7E3p-kc4A.ttf) format('truetype');
}
@font-face {
  font-family: 'Bigelow Rules';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/bigelowrules/v24/RrQWboly8iR_I3KWSzeRuN0zT4c.ttf) format('truetype');
}
#main-qtmt {
  margin-top: 10%;
}
#quote-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  font-family: 'Barlow';
  font-size: 32px;
  background-color: #04ae17;
  margin: 0px auto 0px auto;
  padding: 0px;
}
#author {
  margin: 5px auto 5px auto;
  width: 350px;
  padding: 50px;
  background-color: white;
  font-size: 32px;
  align-self: center;
}
#text {
  margin: 5px auto 5px auto;
  width: 350px;
  padding: 50px;
  font-size: 24px;
  background-color: beige;
  align-self: center;
}
#new-quote {
  background-color: white;
  color: #00ae17;
  margin: 5px auto 5px auto;
  width: 450px;
  padding: 30px;
  font-size: 32px;
  align-self: center;
  border: solid 1px white;
}
#tweet-quote {
  margin: 5px auto 5px auto;
  background-color: #188ee9;
  font-size: 18px;
  color: white;
  padding: 10px;
  border: solid 1px white;
}
#footer {
  display: flex;
  justify-content: center;
  font-family: 'Barlow';
  margin: 5px auto 5px auto;
  font-size: 12px;
}
