@font-face {
  font-family: 'Barlow';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/barlow/v12/7cHqv4kjgoGqM7E3p-kc4A.ttf) format('truetype');
}
@font-face {
  font-family: 'Bigelow Rules';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/bigelowrules/v24/RrQWboly8iR_I3KWSzeRuN0zT4c.ttf) format('truetype');
}
.nav0-box {
  display: flex;
  justify-content: auto;
  flex-wrap: nowrap;
  align-items: center;
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: bold;
  padding-bottom: 10px;
  padding-top: 10px;
  border-bottom: 2px solid #7e4676;
}
.nav0-box .n-link {
  display: flex;
  margin-left: auto;
  margin-right: auto;
}
.nav0-box .n-link:hover {
  background-color: #8987d2;
  color: white;
  padding-left: 30px;
  padding-right: 30px;
}
