@font-face {
  font-family: 'Barlow';
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/barlow/v12/7cHrv4kjgoGqM7E3b8s8.ttf) format('truetype');
}
@font-face {
  font-family: 'Bigelow Rules';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/bigelowrules/v24/RrQWboly8iR_I3KWSzeRuN0zT4c.ttf) format('truetype');
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.block {
  font-family: sans-serif;
  font-size: 20px;
  width: 320px;
  min-height: 50vh;
  max-height: 120vh;
  margin: 10px;
  background-image: linear-gradient(0deg, rgba(240, 130, 200) 90%, rgba(140, 130, 200, 0.8) 100%);
  border-radius: 10px;
}
.app-title {
  font-family: 'Barlow', sans-serif;
  font-size: 32px;
  color: white;
  padding-top: 10px ;
  text-align: center;
}
.display {
  background-color: white;
  width: 300px;
  height: fit-content;
  text-align: end;
  padding: 9px;
  margin: 30px  auto 15px auto;
  font-size: xx-large;
  border: 2px solid #b998ea;
  border-radius: 10px;
}
.inputs-plate {
  display: flex;
  flex: inline;
  background-image: linear-gradient(180deg, rgba(240, 130, 200) 50%, rgba(140, 130, 200, 0.03) 100%);
  padding: 30px 10px 30px 10px;
  margin-left: auto;
  margin-right: auto;
}
.num-block {
  display: grid;
  place-items: center;
  gap: 9px;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: repeat(5, 50px);
  padding-left: 10px;
}
.ops-block {
  display: grid;
  place-items: center;
  gap: 9px;
  grid-template-columns: repeat(1, auto);
  grid-template-rows: repeat(4, 50px);
  padding-left: 20px;
}
.button {
  background-color: white;
  border: 3px solid rgba(140, 130, 200, 0.8);
  min-width: 60px;
  padding: 10px;
  font-size: 24px;
  border-radius: 10px;
}
.service {
  padding-left: 20px;
  text-align: left;
  font-family: 'Barlow', sans-serif;
  font-weight: 100;
}
.small-serv {
  font-size: 10pt;
}
